import { Component, OnInit ,Inject} from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import {DialoguitoComponent} from '../dialoguito/dialoguito.component';


@Component({
  selector: 'app-examensitos',
  templateUrl: './examensitos.component.html',
  styleUrls: ['./examensitos.component.css'],
  providers:[DialoguitoComponent]
  
})
export class ExamensitosComponent implements OnInit {
  simpleDrop: any = null;
  animal: string="Garto";
  name: string="Misufuz";
  oculta:boolean=true;
  constructor(public dialog: MatDialog) {}

  ngOnInit() {
  }

  openDialog(): void {
    let dialogRef = this.dialog.open(DialoguitoComponent, {
      width: '140px',
      data: { name: this.name, animal: this.animal }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      this.animal = result;
    });
  }

  
checarRespuesta(){
  this.oculta=false;
}

}




