import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';

import {FormsModule} from '@angular/forms';
import {HttpModule} from '@angular/http';
import { ClarityModule } from "clarity-angular";
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { InicioComponent } from './principal/inicio/inicio.component';
import { ExamenesComponent } from './examenes/examenes.component';
import { PrincipalComponent } from './principal/principal.component';
import { RegistroalumnoComponent } from './registroalumno/registroalumno.component';
import { RegistroprofesorComponent } from './registroprofesor/registroprofesor.component';
import { ExamensitosComponent } from './examensitos/examensitos.component';
import {MatButtonModule,MatDialogModule, MatCheckboxModule, MatRadioModule,MatButtonToggleModule} from '@angular/material';
import {DndModule} from 'ng2-dnd';
import 'hammerjs';
import { DialoguitoComponent } from './dialoguito/dialoguito.component'; 
@NgModule({
  declarations: [
    AppComponent,
    InicioComponent,
    ExamenesComponent,
    PrincipalComponent,
    RegistroalumnoComponent,
    RegistroprofesorComponent,
    ExamensitosComponent,
    DialoguitoComponent
  ],
  entryComponents: [DialoguitoComponent],
  imports: [
    BrowserModule,
    AppRoutingModule,
    FormsModule,
    HttpModule,
    MatButtonModule,
    MatRadioModule,
    MatCheckboxModule,
    MatDialogModule,
    MatButtonToggleModule,
    BrowserAnimationsModule,
    DndModule.forRoot(),
    ClarityModule.forRoot()
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



